"use client";

import { useState } from "react";
import FeedForm from "./FeedForm"; // Your Client Component for editing the event

interface Event {
  id: string;
  sport_key: string;
  sport_title: string;
  commence_time: string;
  completed: string;
  home_team: string;
  away_team: string;
  scores: string;
  last_updated: string;
}

export default function FeedItem({ event }: { event: Event }) {
  const [isEditing, setIsEditing] = useState(false);

  return (
    <li key={event.id} className="p-4 bg-gray-100 rounded shadow flex flex-col">
      <div className="flex items-center justify-between">
        <div>
          <p className="text-lg font-medium">
            {event.home_team} vs {event.away_team}
          </p>
          <p className="text-sm text-gray-600">
            Commence Time: {new Date(event.commence_time).toLocaleString()}
          </p>
        </div>
      </div>
      {/* Conditionally render the edit form */}
      {isEditing && (
        <div className="mt-2">
          <FeedForm event={event} initialHomeTeam={event.home_team} initialAwayTeam={event.away_team} />
        </div>
      )}
    </li>
  );
}
